// CARD//

.about {
  &__container {
    max-width: 1280px;
    margin: 40px auto;
  }

  &__title {
    text-align: center;
    margin-bottom: 20px;
    font-size: 32px;
    font-weight: 700;
  }

  &__text {
    text-align: center;
    margin-bottom: 40px;
  }

  &__cards {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }

  &__card {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    text-align: center;
    width: 30%;
    transition: transform 0.3s ease;

    &:hover {
      transform: translateY(-10px);
    }
  }

  &__img {
    width: 200px;
    border-radius: 10px;
    margin-bottom: 20px;
  }

  &__h3 {
    margin-bottom: 10px;
  }

  &__p {
    margin-bottom: 20px;
  }

  &__btn {
    display: inline-block;
    padding: 10px 20px;
    background-color: #313131;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: #c2dfa7;
      color: #2e2e2e;
    }
  }
}

@media (max-width: 768px) {
  .about {
    &__cards {
      flex-direction: column;
      align-items: center;
    }

    &__card {
      width: 80%;
      margin-bottom: 20px;
    }

    &__img {
      width: 150px;
    }
  }
}

@media (max-width: 480px) {
  .about {
    &__container {
      margin: 20px auto;
    }

    &__title {
      margin-bottom: 10px;
    }

    &__text {
      margin-bottom: 20px;
    }

    &__card {
      width: 100%;
      padding: 15px;
    }

    &__img {
      width: 120px;
    }

    &__h3 {
      font-size: 18px;
    }

    &__p {
      font-size: 14px;
    }

    &__btn {
      padding: 8px 16px;
      font-size: 14px;
    }
  }
}

#openModalBtn {
  display: inline-block;
  padding: 10px 20px;
  background-color: #313131;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  &:hover {
    background-color: #c2dfa7;
    color: #2e2e2e;
  }
}

@media (max-width: 480px) {
  #openModalBtn {
    padding: 8px 16px;
    font-size: 14px;
  }
}

// Модальное окно
.modalContact {
  &--open {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4); // Полупрозрачный оверлей
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4); // Полупрозрачный оверлей
    cursor: pointer; // Курсор указывает, что оверлей кликабелен
  }

  &__content {
    position: relative;
    background-color: #fefefe;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
    max-width: 600px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    z-index: 1001; // Контент модального окна должен быть выше оверлея
  }

  &__close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
    cursor: pointer;
    width: 50px;
    height: 50px;
    background-color: unset;
    border: navajowhite;

    &:hover,
    &:focus {
      color: black;
      text-decoration: none;
      cursor: pointer;
    }
  }

  &__h2 {
    margin-top: 0;
  }

  &__p {
    margin-top: 10px;
    margin-bottom: 10px;
  }
}

// Стили для формы внутри модального окна
form {
  display: flex;
  flex-direction: column;
}

input[type='email'] {
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

button[type='submit'] {
  padding: 10px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;

  &:hover {
    background-color: #45a049;
  }
}
