// Слайдер //

.slider {
  &__title {
    text-align: center;
    margin-top: 20px;
  }

  &__container {
    position: relative;
    width: 100%;
    max-width: 1280px;
    height: 550px;
    margin: 0 auto;
    overflow: hidden;
    margin-top: 20px;

    @media (max-width: 768px) {
      height: 400px;
    }

    @media (max-width: 480px) {
      height: 250px;
    }
  }

  &__sliders {
    display: flex;
    transition: transform 0.5s ease-in-out;
  }

  &__sliders img {
    width: 100%;
    height: 550px;
    object-fit: cover;
    border-radius: 40px;

    @media (max-width: 768px) {
      height: 400px;
    }

    @media (max-width: 480px) {
      height: 250px;
    }
  }

  &__prev-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 100px;
    height: 100px;
    background-color: transparent;
    border: none;
    font-size: 24px;
    color: #fff;
    cursor: pointer;

    @media (max-width: 480px) {
      width: 30px;
      height: 30px;
      font-size: 18px;
    }
  }

  &__next-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 100px;
    height: 100px;
    background-color: transparent;
    border: none;
    font-size: 24px;
    color: #fff;
    cursor: pointer;

    @media (max-width: 480px) {
      width: 30px;
      height: 30px;
      font-size: 18px;
    }
  }

  &__prev-button {
    left: 10px;
  }

  &__next-button {
    right: 10px;
  }
}
