// история города//

.history {
  padding: 40px 0;

  &__container {
    max-width: 1280px;
    margin: 0 auto;
    padding: 0 20px;
  }

  &__title {
    font-size: 36px;
    font-weight: bold;
    color: #333;
    margin-bottom: 20px;
    text-align: center;
  }

  &__text {
    font-size: 18px;
    line-height: 1.6;
    color: #555;

    &-title {
      font-size: 24px;
      font-weight: bold;
      color: #444;
      margin-bottom: 15px;
    }

    &-txt {
      p {
        margin-bottom: 20px;

        span {
          font-weight: bold;
          // color: #007bff;
          color: #7eca37;
        }
      }
    }
  }
}

// адаптив истории
@media (max-width: 768px) {
  .history {
    &__title {
      font-size: 28px;
    }

    &__text {
      font-size: 16px;

      &-title {
        font-size: 20px;
      }
    }
  }
}

@media (max-width: 480px) {
  .history {
    &__title {
      font-size: 24px;
    }

    &__text {
      font-size: 14px;

      &-title {
        font-size: 18px;
      }
    }
  }
}
